/* fonts  */

@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap');
html {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    height: 100vh;
    scroll-behavior: smooth;
}

body {
    background-image: url(/src/images/pawel-czerwinski-C-pmBkHZWQ0-unsplash.jpg);
    background-size: 100%;
    background-repeat: no-repeat;
    background-attachment: fixed;
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: 'Open Sans', sans-serif;
}

header {
    height: 100vh;
    width: 20vh;
    background-color: rgba(22, 17, 17, 0.5);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(500px);
    position: fixed;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    z-index: 2;
}

.heading img {
    position: fixed;
    width: 20vh;
}

.center {
    height: 100%;
    width: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid {
    position: fixed;
    font-size: 2.5vh;
}

.grid div {
    transition: 0.2s;
    width: 20vh;
    padding: 3vh 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid div:hover {
    background: linear-gradient(to left, #4e1e41, #4a646c);
}

.grid div a {
    color: rgba(68, 186, 186, 0.5);
    text-decoration: none;
}


/* main part */

main {
    position: absolute;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-areas: 'empty main main main' 'empty main main main' 'empty skills skills skills' 'empty contact contact contact';
    grid-gap: 3vh;
    height: 150vh;
    width: 85vw;
    /* z-index: -1; */
    margin-top: 5vh;
    color: rgba(68, 186, 186, 0.5);
    /* right: 100vh; */
}

#Home {
    /* color: white; */
    grid-area: main;
    display: flex;
    flex-direction: column;
    justify-content: top;
    align-items: center;
    /* height: 150vh; */
    text-align: center;
    background-color: rgba(22, 17, 17, 0.5);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    border-radius: 2vh;
    font-size: 1.9vh;
}

.projects {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 3vh;
    height: 80%;
    width: 50%;
    padding: 2vh 0 0 0;
}

.projects img {
    width: 15vh;
    height: 15vh;
    /* z-index: 1; */
    border-radius: 1vh;
}

#Skills {
    /* color: white; */
    grid-area: skills;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* height: 150vh; */
    text-align: center;
    background-color: rgba(22, 17, 17, 0.5);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    padding: 2.5vh 4vh;
    border-radius: 2vh;
    font-size: 1.9vh;
}

#Contact {
    /* color: white; */
    grid-area: contact;
    display: flex;
    flex-direction: column;
    justify-content: top;
    align-items: center;
    text-align: center;
    background-color: rgba(22, 17, 17, 0.5);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    padding-top: 2.5vh;
    border-radius: 2vh;
    margin-bottom: 5vh;
}

footer {
    width: 100%;
    height: 6vh;
    position: fixed;
    bottom: 0;
    background-color: rgba(22, 17, 17, 0.5);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

footer div {
    padding: 0 2vh;
}

footer img {
    height: 4vh;
    width: 4vh;
    background-color: rgb(107, 83, 83);
    border-radius: 50%;
}